<template>
    <div>
        <div class="container">
       <div class="main__items" v-if="posts">
        <div class="main__items-item" v-for="post in posts" :key="post.id">
            <router-link :to="/Post/ + post.id" ><a href="">{{ post.title }}</a></router-link>
            <p>Body: {{ post.body }}</p>
        </div>
       </div>
       <div v-else>
            Идет загрузка...
       </div>
    </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                posts: ''
            }
        },
        created() {
            this.getPosts()
        },
        methods: {
            async getPosts() {
                let res = await fetch('https://jsonplaceholder.typicode.com/posts')
                let infos = await res.json()
                this.posts = infos
            }
        }
    }
</script>

